import React,{useState, useEffect} from 'react';
// import Core UI
import {
  CCard,
  CCol,
  CRow,
  CCardHeader,
  CCardBody,
  CButton,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CForm,
  CFormGroup,
  CLabel,
  CInput,
  CFormText
} from '@coreui/react';
import MaterialTable from 'material-table'

import CIcon from '@coreui/icons-react'
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import SVCMasterWil from '../services-wil';
import swal from 'sweetalert';
import Swal from 'sweetalert2';

const Provinsi = () => {

    const [getModalProv, setModaProv] = useState(false);
    const [dataVal, setData] = useState({
      IdProvinsi : '',
      Kode : '',
      Nama : ''
    });
    const [GetProvinsiAPI, setProvinsiAPI] = useState([]);
    const [GetStatusProvinsiApi, SetStatusProvinsiApi] = useState(false);
    const [GetIsUpdate, SetIsUpdate] = useState(true);


    const handleModalProv = () =>{
      setModaProv(true);
      SetIsUpdate(false);
      setData({
        IdProvinsi : '',
        Kode : '',
        Nama : ''
      })
    }


    const handleChange = (event) => {

        let dataValNew = {...dataVal};
        dataValNew[event.target.name] = event.target.value;
        setData(dataValNew);
      };

    const handlePostProvinsi = () => {
      if(GetIsUpdate){
        SVCMasterWil.EditProvinsiWeb(dataVal).then(result => {
          if(result.isSuccess){
            swal("Provinsi Update","Data Provinsi berhasil dirubah","success");
          }
          setModaProv(!getModalProv);
          GetProvinsisApis();
        });
      }else{
        let param = `Kode=${dataVal.Kode}&Nama=${dataVal.Nama}`;
        SVCMasterWil.AddProvinsi(param).then((result) => {
          if(result.isSuccess){
            swal("Provinsi Add","Data Provinsi berhasil ditambah","success");
          }else{
             Swal.fire('Provinsi Tidak dapat ditambah', `${result.returnMessage}`, 'error')
          }
          setModaProv(!getModalProv);
          GetProvinsisApis();
        });

      }
    }

    const GetProvinsisApis = () =>{
      SVCMasterWil.GetProvinsis().then(result => {
          let tmpArr = [];
          let resultData = result.data;
          resultData.map(val => {
          tmpArr.push(
                {
                  "id" : val.idProvinsi,
                  "kode": val.kode,
                  "nama":val.nama

                }
              );
          });
          return setProvinsiAPI(tmpArr);
      });

    }

    const handleGetIdProv = (idProv) =>{
      let param = `?IdProvinsi=${idProv}`;
      SVCMasterWil.GetProvinsi(param).then(result => {
          if(result.isSuccess){

              let resultData = result.data;
              let setDataNew = {...dataVal}
              setDataNew.IdProvinsi = result.data.idProvinsi;
              setDataNew.Kode = resultData.kode;
               setDataNew.Nama = resultData.nama;
                setData(setDataNew);
              setModaProv(!getModalProv);
              SetIsUpdate(true);
          }
      })
    }

    const handleDeleteId = (idProv, nama)=> {
      console.log(idProv)
      swal({
        title: "Konfirmasi Hapus?",
        text: `Provinsi ${nama} akan dihapus`,
        icon: "warning",
        buttons: true,
        dangerMode: true,
      })
      .then((willDelete) => {
        if (willDelete) {
          SVCMasterWil.HapusProvinsi(`IdProvinsi=${idProv}`).then(result => {
              if(result.isSuccess){
                swal(`Provinsi ${nama} telah dihapus`, {
                icon: "success",
              });
              GetProvinsisApis();
              }
          });

        } else {
          swal("Data tidak dihapus");
        }
      });
    }

    const processDelete = (idProv)=> {

    }



    useEffect(() =>{
      GetProvinsisApis();
    }, []);
    return(
     <CRow>
        <CCol md="12" xl="12" xs="12">
            <CCard>
                <CCardHeader>
                  <CCol md="3" >
                        <CButton
                          block
                          color="primary"
                           onClick={handleModalProv}>
                            <FontAwesomeIcon
                            icon="plus"/>
                           Tambah
                        </CButton>
                  </CCol>
                </CCardHeader>
                <CCardBody>
                    <MaterialTable
                      title=""
                      options={{
                        headerStyle: {
                          backgroundColor: '#01579b',
                          color: '#FFF'
                        },
                        filtering:true
                      }}
                      columns={[
                        { title: 'ID Provinsi', field: 'id', type: 'numeric',visible: false ,
                          cellStyle: {
                            backgroundColor: '#039be5',
                            color: '#FFF'
                          },
                          headerStyle: {
                            backgroundColor: '#039be5',
                          }
                        },
                        { title: 'Kode', field: 'kode' },
                        { title: 'Nama Provinsi', field: 'nama',  },

                      ]}
                    data={GetProvinsiAPI}

                  actions={[
                    {
                      icon: 'edit',
                      tooltip: 'Edit Provinsi',
                      text:'Update',
                      label:'Update',
                      onClick: (event, rowData) => {
                          handleGetIdProv(rowData.id)
                      }
                    },
                    {
                      icon: 'delete',
                      tooltip: 'Delete Provinsi',
                      onClick: (event, rowData) => {
                        handleDeleteId(rowData.id, rowData.nama)
                      }
                    }
                  ]}
                  />
                </CCardBody>
            </CCard>
        </CCol>

        <CModal
              show={getModalProv}
              onClose={() => setModaProv(!getModalProv)}
              size="lg"
            >
              <CModalHeader closeButton>
                <CModalTitle>Form Provinsi</CModalTitle>
              </CModalHeader>
              <CModalBody>
                  <CForm>
                    <CFormGroup>
                      <CLabel htmlFor="nf-kodeProv">Kode Provinsi</CLabel>
                      <CInput type="text" id="nf-kode" value={dataVal.Kode} name="Kode" placeholder="Input Kode.." onChange={(e) => handleChange(e)} />
                    </CFormGroup>
                    <CFormGroup>
                      <CLabel htmlFor="nf-nama"> Nama Provinsi</CLabel>
                      <CInput type="text" id="nf-nama" value={dataVal.Nama} name="Nama" placeholder="Input Nama Provinsi .." onChange={(e) => handleChange(e)} ></CInput>
                    </CFormGroup>
                  </CForm>
              </CModalBody>
              <CModalFooter>
                <CButton color="primary" onClick={handlePostProvinsi}>Simpan</CButton>
                <CButton color="secondary" onClick={() => setModaProv(!getModalProv)}>Cancel</CButton>
              </CModalFooter>
        </CModal>
     </CRow>
    )
}

export default Provinsi;
